body {
  font-family: sans-serif;
}
#app {
  margin: 0px;
  height: 100%;
  width: 100%;
  /* background-color: red; */
}
#container {
  height: 50%;
  width: 300px;
  margin: 0px auto;
  align-items: center;
  justify-content: center;
  /* border: 2px solid black; */
  display: flex;
  flex-wrap: wrap;
  /* background-color: blue; */
}
#shape-container {
  height: 50%;
  width: 100%;
  /* background-color: green; */
  /* border: 2px solid black; */
  display: flex;
  align-items: center;
  justify-content: center;
}
#circle {
  height: 250px;
  width: 250px;
  border-radius: 50%;
  margin: 10%;
  background-color: blue;
  /* border: 2px solid yellow; */
}
/* #shape {
  height: 0px;
  width: 0px;
  border-top: 175px solid teal;
  border-left: 175px solid yellow;
  margin: 36px auto;
  position: relative;
} */
#shape {
  height: 70%;
  width: 70%;
  margin: 15% auto;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.5s ease-in-out;
}
#button-container {
  height: 50%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  /* background-color: grey; */
}
#button {
  margin: 25%;
  /* border: 2px solid yellow; */
  padding: 5%;
}
.btn {
  height: 50px;
  width: 150px;
  margin: 5px;
  font-family: consolas;
  font-weight: 800;
  font-size: 1rem;
}
.btn:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  text-align: center;
}
